import React from 'react';
import {BrowserRouter as Router, Route, Link} from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';

import Footer from './components/Footer'
import HomePage from './pages/HomePage'
import AboutPage from './pages/AboutPage'
import ContactPage from './pages/ContactPage'

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      title: 'Pieter Bikkel',
      headerLinks: [
        {title: 'Home', path: '/website'},
        {title: 'About', path: '/website/about'},
        {title: 'Contact', path: '/website/contact'}
      ],
      home: {
        title: 'Developing Apps',
        subTitle: 'that make a difference',
        subText: 'Check out my apps below' 
      },
      about: {
        title: 'About Me',
      },
      contact: {
        title: 'Let\'s Talk',
      }
    }
  }
  render() {
    return (
      <Router>
        <Container className="p-0" fluid={true}>
          <Navbar className="border-bottom" bg="transparent" expand="lg">
            <Navbar.Brand>Pieter Bikkel</Navbar.Brand>
            <Navbar.Toggle className="border-0" aria-controls="navbar-toggle"/>
            <Navbar.Collapse id="navbar-toggle">
              <Nav className="ml-auto">
                <Link className="nav-link" to="/website/">Home</Link>
                <Link className="nav-link" to="/website/about">About</Link>
                <Link className="nav-link" to="/website/contact">Contact</Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
          <Route path="/website/" exact render={() => <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle}/>} text={this.state.home.subText} />
          <Route path="/website/about" render={() => <AboutPage title={this.state.about.title} /> } />
          <Route path="/website/contact" render={() => <ContactPage title={this.state.contact.title} />} />

          <Footer />

        </Container>
      </Router>
      );
  }
}

export default App;
