import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';

import volleybal from '../assets/images/volleybal.jpeg';
import programming from '../assets/images/programming.jpeg';
import motogp from '../assets/images/motogp.png';
import HorizontalCard from '../components/HorizontalCard';

class AboutCards extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            items: [
                {
                    id: 0,
                    title: 'Volleybal',
                    subTitle: 'One of my hobbies is playing volleyball at Dash. In the team I am a setter.',
                    imgSrc: volleybal,
                    link: 'https://www.vvdash.nl',
                },
                {
                    id: 1,
                    title: 'MotoGP',
                    subTitle: 'Another hobby of mine is watching MotoGP. The exiting races are a pleasure to watch on Sundays.',
                    imgSrc: motogp,
                    link: 'https://www.motogp.com',
                },
                {
                    id: 2,
                    title: 'Tech',
                    subTitle: 'Lastly I am passionate about programming and many other tech related things. I always want to keep up with the latest and greatest tech innovations.',
                    imgSrc: programming,
                    link: 'https://ifixit.com',
                }
            ]
        }
    }

    makeItems = (items) => {
        return items.map(item => {
            return <HorizontalCard item={item} key={item.id} />
        })
    }

    render() {
        return(
            <Container fluid={true}>
                <Row className="justify-content-around">
                    {this.makeItems(this.state.items)}
                </Row>
            </Container>
        );
    }
}

export default AboutCards;